.MovieDetails {
  display: flex;
  align-items: center;
}

.movie-info {
  justify-content: center;
  margin: 5% auto;
  width: 45%;
  height: 45%;
  padding: 10px;
  text-align: center;
  border: 6px solid $border-color;
  background-color: $opaque-white;
  color: $text-color;
  box-shadow: $offset-shadow;
}

.movie-title {
  padding: 10px;
  text-align: center;
  font-size: 40px;
  font-family: $heading-font;
}

.movie-image {
  display: inline-block;
  margin: 3% auto;
  height: 600px;
  border: 5px solid $off-white;
}

.movie-overview {
  height: auto;
  margin: 10px 20px;
  padding: 25px;
  background-color: $opaque-white;
  border: 2px solid $steel-grey;
  border-radius: 10px;
  font-style: italic;
  font-size: 20px;
}

.movie-details {
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
}

.detail-bold {
  font-family: $heading-font;
  font-size: 19px;
}

@media screen and (max-width: 910px) {
  .MovieDetails {
    display: grid;
    grid-template-rows: 650px 1fr;
  }

  .movie-info {
    width: 400px;
    height: 95%;
  }
}

@media screen and (max-width: 450px) {
  .MovieDetails {
    display: grid;
    grid-template-rows: 650px 1fr;
  }

  .movie-image {
    width: 90%;
  }

  .movie-info {
    width: 90%;
    height: 95%;
  }
}