*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  height: 100%;
  width: 100%; }

body {
  background-image: url(/movie-tracker/static/media/background.c8c1eb0c.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover; }

a {
  text-decoration: none;
  color: #062626; }

label {
  padding-bottom: 10px;
  font-size: 20px; }

input {
  display: block;
  margin-bottom: 10px;
  font-size: 20px;
  padding: 8px; }

a:active {
  color: #8F9DC3; }

.App {
  font-family: "Dosis";
  color: "Dosis"; }

.title-section {
  text-align: center;
  padding: 0px 10px;
  background-image: url(/movie-tracker/static/media/title-background.760f7f08.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Fredoka One";
  border-top: 4px solid #164946; }

.app-title {
  display: inline-block;
  padding-top: 20px;
  font-size: 60px;
  margin: 0px;
  text-shadow: 2px 2px #D3CEED;
  font-weight: bold; }

.app-subtitle {
  padding: 10px 0px;
  color: white;
  text-shadow: 3px 3px black;
  font-style: italic;
  font-size: 22px;
  font-family: "Dosis";
  letter-spacing: 1px; }

.app-title:hover {
  color: white;
  text-shadow: 3px 3px black; }

.notice {
  text-align: center;
  margin-top: 20px;
  font-size: 18px;
  color: #884133; }

@media screen and (max-width: 430px) {
  .app-title {
    font-size: 48px;
    padding-top: 34px; }
  .app-subtitle {
    font-size: 18px;
    padding: 12px 0px; } }

.SignUpForm {
  width: 430px;
  height: 700px;
  margin: 30px auto;
  display: flex;
  padding: 25px 20px;
  flex-direction: column;
  font-family: "Dosis";
  background-color: rgba(243, 241, 255, 0.6);
  border: 6px solid #164946;
  box-shadow: 12px 12px rgba(243, 241, 255, 0.25); }

.div--email {
  display: flex;
  flex-direction: column;
  height: 215px; }

.input--red {
  outline-color: red;
  border: 2px solid red; }

.div--password {
  margin: 30px 0px;
  display: flex;
  flex-direction: column;
  height: 215px; }

.signup-title {
  text-align: center;
  margin-bottom: 15px;
  font-family: "Fredoka One";
  font-size: 40px;
  text-shadow: 2px 2px #f3f1ff; }

.create-button {
  width: 60%;
  margin: 0px auto;
  padding: 8px;
  font-size: 22px;
  text-transform: capitalize;
  font-weight: bold;
  border: 3px solid #164946;
  border-radius: 5px;
  background-color: #8F9DC3;
  color: #f3f1ff;
  letter-spacing: 1px; }

.create-button:hover {
  background-color: #D3CEED;
  color: #062626;
  cursor: pointer; }

@media screen and (max-width: 460px) {
  .SignUpForm {
    width: 95%; } }

.LoginForm {
  width: 430px;
  height: 400px;
  margin: 30px auto;
  display: flex;
  padding: 25px 20px;
  flex-direction: column;
  font-family: "Dosis";
  background-color: rgba(243, 241, 255, 0.6);
  border: 6px solid #164946;
  box-shadow: 12px 12px rgba(243, 241, 255, 0.25); }

.login-form-title {
  text-align: center;
  margin-bottom: 15px;
  font-family: "Fredoka One";
  font-size: 40px;
  text-shadow: 2px 2px #f3f1ff; }

.login-button {
  width: 60%;
  margin: 15px auto 0px auto;
  padding: 8px;
  font-size: 22px;
  text-transform: capitalize;
  font-weight: bold;
  border: 3px solid #164946;
  border-radius: 5px;
  background-color: #8F9DC3;
  color: #f3f1ff;
  letter-spacing: 1px; }

.login-button:hover {
  background-color: #D3CEED;
  color: #062626;
  cursor: pointer; }

@media screen and (max-width: 460px) {
  .LoginForm {
    width: 95%; } }

.MovieContainer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  justify-items: center;
  border-right: 3px solid #164946;
  border-left: 3px solid #164946;
  padding-bottom: 50px; }

@media screen and (max-width: 1080px) {
  .MovieContainer {
    grid-template-columns: repeat(3, 1fr); } }

@media screen and (max-width: 720px) {
  .MovieContainer {
    grid-template-columns: 1fr 1fr; } }

@media screen and (max-width: 550px) {
  .MovieContainer {
    grid-template-columns: 1fr; } }

.MovieCard {
  display: grid;
  background-color: rgba(243, 241, 255, 0.6);
  border: 7px solid #164946;
  border-radius: 10px;
  text-align: center;
  margin: 15px 0px;
  width: 90%;
  color: "Dosis"; }

.moviecard-image {
  float: left;
  width: 100%; }

.moviecard-title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 1.4rem;
  font-family: "Fredoka One";
  padding: 10px 10px;
  height: 75px;
  vertical-align: middle; }

.moviecard-favorite {
  float: left;
  padding: 5px;
  width: 100%;
  font-family: "Dosis";
  font-size: 26px;
  font-weight: bold;
  background-color: rgba(243, 241, 255, 0.7);
  word-spacing: 20px;
  letter-spacing: 1px;
  border: 3px solid #8F9DC3; }

.MovieCard:hover {
  box-shadow: 4px 4px 15px #f3f1ff; }

.MovieCard:hover h3 {
  color: white;
  text-shadow: 3px 3px black; }

.moviecard-favorite:hover {
  background-image: linear-gradient(white 20%, #edd3ce 60%);
  cursor: pointer; }

.MovieDetails {
  display: flex;
  align-items: center; }

.movie-info {
  justify-content: center;
  margin: 5% auto;
  width: 45%;
  height: 45%;
  padding: 10px;
  text-align: center;
  border: 6px solid #164946;
  background-color: rgba(243, 241, 255, 0.6);
  color: #062626;
  box-shadow: 12px 12px rgba(243, 241, 255, 0.25); }

.movie-title {
  padding: 10px;
  text-align: center;
  font-size: 40px;
  font-family: "Fredoka One"; }

.movie-image {
  display: inline-block;
  margin: 3% auto;
  height: 600px;
  border: 5px solid #f3f1ff; }

.movie-overview {
  height: auto;
  margin: 10px 20px;
  padding: 25px;
  background-color: rgba(243, 241, 255, 0.6);
  border: 2px solid #8F9DC3;
  border-radius: 10px;
  font-style: italic;
  font-size: 20px; }

.movie-details {
  padding: 10px;
  font-size: 20px;
  font-weight: bold; }

.detail-bold {
  font-family: "Fredoka One";
  font-size: 19px; }

@media screen and (max-width: 910px) {
  .MovieDetails {
    display: grid;
    grid-template-rows: 650px 1fr; }
  .movie-info {
    width: 400px;
    height: 95%; } }

@media screen and (max-width: 450px) {
  .MovieDetails {
    display: grid;
    grid-template-rows: 650px 1fr; }
  .movie-image {
    width: 90%; }
  .movie-info {
    width: 90%;
    height: 95%; } }

.LoginPrompt {
  z-index: 1;
  top: 30%;
  left: 35%;
  height: 170px;
  width: 430px;
  position: absolute;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  background-color: rgba(243, 241, 255, 0.9);
  border: 6px solid #8F9DC3; }

.close-button,
.loginprompt-button,
.signupprompt-button {
  font-weight: bold;
  border: 2px solid #164946;
  cursor: pointer; }

.close-button {
  float: left;
  margin: 5px;
  padding: 2% 6%; }

.login-title {
  font-size: 22px; }

.button-container {
  position: relative;
  text-align: center;
  margin: 12% auto; }

.loginprompt-button,
.signupprompt-button {
  margin: 20px;
  padding: 3% 8%;
  display: inline-block;
  font-size: 20px;
  font-weight: bold; }

.loginprompt-button:hover,
.signupprompt-button:hover {
  background-color: #edd3ce;
  color: white; }

.close-button:hover {
  background-color: #062626;
  color: #f3f1ff; }

@media screen and (max-width: 1080px) {
  .LoginPrompt {
    left: 30%; } }

@media screen and (max-width: 720px) {
  .LoginPrompt {
    left: 19%; } }

@media screen and (max-width: 550px) {
  .LoginPrompt {
    left: 11%;
    width: 80%;
    height: 160px; }
  .close-button {
    float: left;
    margin: 5px;
    padding: 1% 2%; }
  .login-title {
    font-size: 16px;
    padding-top: 11px; }
  .loginprompt-button,
  .signupprompt-button {
    margin: 10px 15px;
    padding: 2% 8%; } }

.NavBar {
  grid-column: 1 / 5;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  height: 50px;
  top: 0;
  background-color: #808daf;
  background-image: linear-gradient(to right, #b3afcc, #8F9DC3, white 95%); }

.user-links {
  display: flex;
  align-items: center;
  padding: 0 20px;
  margin: auto;
  height: 50px;
  font-family: "Dosis";
  font-size: 20px;
  color: #062626;
  border-top: 4px solid black;
  border-bottom: 2px solid black; }

.nav-left {
  display: flex;
  justify-content: space-around; }

.nav-right {
  margin-left: auto; }

#item-left,
#item-right {
  padding: 10px;
  color: #f3f1ff; }

#logout-link {
  padding: 10px;
  color: black; }

.greeting {
  padding: 10px; }

.username {
  padding: 0px 5px;
  font-weight: bold; }

#item-left:hover,
#item-right:hover,
.logout-link:hover {
  background-color: #f3f1ff;
  color: #062626; }

#logout-link:hover {
  background-color: #062626;
  color: #f3f1ff; }

@media screen and (max-width: 520px) {
  #item-left,
  #item-right {
    padding: 13px 8px; }
  .user-links {
    font-size: 16px;
    padding: 0; }
  .nav-right p {
    font-size: 14px; } }

